import React,{ useState, useEffect, useContext } from 'react';
import axios from 'axios';
import { useForm } from 'react-hook-form';
import { Alert } from 'react-bootstrap';
import styles  from  './AddProduct.module.css';

const Feedback = (props)=>{
	const success = props.success;
	const errMsg = props.errMsg;
	if (success) {
		return <Alert variant="success">Created product record.</Alert>;
	} else if (errMsg) {
		return <Alert variant="danger">{errMsg}</Alert>;
	}
};

function AddProduct() {


  

    const [errMsg, setErrMsg] =useState('');
    const [success, setSuccess] = useState(false);

  	const formOptions = {
      productName: { required: 'Product name required.' }
    };  
	const {
		register,
		handleSubmit,
    setFocus,
    /** https://stackoverflow.com/questions/62981145/react-hook-form-know-when-value-is-modified */
		formState: { errors,isSubmitting,isValidating },
	} = useForm();
  




    useEffect(() => {
      console.log('>>>useEffect>>>has executed to setFocus(\'productName\')')
       setFocus('productName');
      }, [setFocus]);

      useEffect(() => {
        setErrMsg('');
        setSuccess(false);
        console.log('>>>useEffect>>>has executed to setErrMsg(\'\')');
      }, [isValidating ]);

    const onSubmit =async (data)  => {
      console.log('>>>Inspect the data variable inside the handleSubmitForm method>>>');
      console.log(data);
        try {
          const response = await axios.post(
            'http://localhost:8088/api/products',
            JSON.stringify({ productName : data.productName }),
            {
              headers: { 'Content-Type': 'application/json' },
              withCredentials: true,
            }
          );
          const status = response?.data?.status;
          if (status==='success'){
             setSuccess(true);
          }
        } catch (err) {
          setSuccess(false);
          if (!err?.response) {
            setErrMsg('No server response.');
          } else if (err.response?.status === 400) {
            setErrMsg('Missing parameters.');
           } else {
            console.log(' setErrMsg(\'Create record failed.\');')
            setErrMsg('Create record failed.');
          }
        }
      };
      //End of onSubmit 
      //(which is used inside  <form onSubmit={handleSubmit(handleSubmitForm)}>)

     

      return (
          <div className="vh-100 d-flex justify-content-center align-items-center border border-secondary">
            <div className="card w-50">
              <div className="card-body">
                <h5 className="card-title">Add product</h5>
                <div className={styles.MessageBoxFixedHeight} >
                <Feedback success={success} errMsg={errMsg} />
                </div>
                <Alert variant="info" className="border">
                  Provide product details
                </Alert>
              <form onSubmit={handleSubmit(onSubmit)}>


              <div className="form-group row justify-content-center">
							<label  className="col-sm-2 col-form-label text-end" htmlFor="productName">Product name</label>
                            <div className="col-sm-6">                         
							<input
								type="text"
								autoComplete="off"
								className="form-control form-control-sm"
								{...register('productName', formOptions.productName)}
							/>
							<small className="m-2 text-danger">
                                {/** https://youtu.be/FY8sXCsjvf8?t=1021  */}
								{errors?.productName && errors.productName.message}
							</small>
                            </div>   
						</div>
						<div className="d-flex flex-row-reverse">
							<button className="btn btn-primary mt-1" disabled={isSubmitting}>Submit</button>
						</div>
					</form>
				</div>
				{/* card-body */}
			</div>
			{/* card */}
			{/*vh-100 d-flex justify-content-center align-items-center border border-secondary */}
		</div>
      )
}

export default AddProduct